.message {
border: 1px solid red;
    width: 575px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-left: 635px;

}

.imageAndText {
    border: 1px solid black;
    display: flex;
    flex-direction: row-reverse;

}

.imageAndText img {
    width: 48px;
    height: 48px;
    display: flex;

}

.text {
    box-shadow: 0px 1px 2px 0px rgba(29, 33, 38, 0.1),0px 5px 20px 0px rgba(29, 33, 38, 0.03);
    background: rgb(0, 102, 204);
    width: 527px;
    padding: 5px 15px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    border: 1px yellow solid;
}

.text:after {

}

.name {
    color: rgb(255, 255, 255);
    font-family: Montserrat, sans-serif;
    font-size: 18px;
    font-weight: 600;
    line-height: 22px;
    letter-spacing: 0;
    text-align: left;
    padding-bottom: 3px;
}

.messageText {
    color: rgb(255, 255, 255);
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0;
    text-align: left;
}

.time {
    color: rgb(0, 0, 0);
    font-family: Montserrat, sans-serif;
    font-size: 10px;
    font-weight: 600;
    line-height: 12px;
    letter-spacing: 0;
    text-align: left;
    display: flex;
    align-content: flex-end;
    border: violet 1px solid;

    margin-right: 10px;
}